import Nav from '../../components/nav/Nav.vue';
export default {
	components: {
		Nav
	},
	data() {
		return {
			username: '',
			password: ''
		};
	},
	methods: {
		reigsterUser() {
			uni.request({
				url:'/api/appusers/register',
				// url: 'http://localhost:3000/appusers/register',
			method:'POST',
				data: {
					username:this.username,
					password:this.password
				},
				header: {
					'custom-header': 'hello' //自定义请求头信息
				},
				success: res => {
					if(res.data){
						//注册成功跳转到登录
						uni.navigateTo({
							url: '/pages/Login/Login'
						});
					}
					
				}
			});
			
		}
	}
};
